@media screen and (min-width: 670px) {
    .siteLayout {
        margin: 20px;
    }
    .logoName {
            float: left;
            width: 120px;
            height: 31px;
            line-height:31px;
            margin: 16px 0;
    }
}

@media screen and (max-width: 670px) {
    .siteLayout {
        margin:10px
    }
    .displayNone {
        display: none;
    }
    .headerLeft {
        width: 100%;
        text-align: center;
    }
    .logoName {
        float: left;
        width: 100%;
        height: 31px;
        line-height:31px;
        margin: 16px 0;
    }
}